  pointer-events: none;
  position: fixed;
  inset: 0; /*Needed???*/
  opacity: 0;
}
.modal {
  background-color: var(--color-eggshell);
  display: grid;
  gap: 1em;

  width: fit-content;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
  pointer-events: none;
  opacity: 0;
  padding: 2em;
  border: 1px solid gray;

  transition:
    transform 175ms ease-out,
    opacity 200ms ease;
}

.modal > * {
  /* border: 2px solid lightgray; */
}

.modal.show {
  opacity: 1;
  pointer-events: all;
  transform: translate(-50%, -50%);
}
.backdrop.show {
  opacity: 0.3;
}

.modal__create-project-container {
  display: none;
  /* height: 0; */
}

option[data-action="create-project"] {
  font-weight: 900;
}
